.feedbackThumb {
    margin-top: 10px;
    margin-bottom: 10px;
  }

  .feedbackButton {
    background-color: Azure;
    border: transparent 1px solid;
    border-radius: 4px;
    padding: 10px;
    width: 70px;
    cursor: pointer;
  }
  
  

  .feedbackButton:not(:last-child) {
    margin-right: 10px;
  }
  
  .feedbackButton:hover,
  .feedbackButton:focus {
    background-color: rgb(194, 193, 193);
  }
